<script>
	import common from '../../common/info.js'
	export default {
		data() { 
			return {
				signatures:'',
				curRoutes:'',
				infoList:'',
				values:'',
				showes:false,
				lists:'',
				openId:'',
				tokens:'',
				userInfos:'',
				articles:'',
				miniapps:'',
				videos:'',
				allarticles:'',
				top_background: "background:none;color:#fff;",
				top_background_imgurl: "../../static/assets/arrow-left-bold1.png",
				top_style: "",
				top_height: "",
				autograph:'',
			}	
			
		},
		
		created() {
			//获取胶囊按钮的数据
			console.log(uni.getMenuButtonBoundingClientRect());
			let topInfo = uni.getMenuButtonBoundingClientRect();
			let iMheight = (topInfo.height - 40) / 2;
			this.top_height = "height:" + (topInfo.top + iMheight) + "px";
			this.top_style = "top:" + (topInfo.top + iMheight) + "px";
		},
		onLoad() {
			this.getEntryList();   //获取个人中心菜单
			let routes = getCurrentPages();
			let curRoute = routes[routes.length - 1].route;
			this.curRoutes = curRoute;
			uni.request({
				url: common.WEBSITE_URL + '/api/competition/getConfig',
				success: (res) => {
					if (res.data.code == 0) {
						this.infoList = res.data.data;
					}
				}
			});
		},
		onShareAppMessage(res) {
			return{
				title:this.infoList.share_title,
				path:this.curRoutes,
				imageUrl:this.infoList.share_cover,
			}
		},
		onPullDownRefresh() {
			setTimeout(()=>{
				uni.reLaunch({
					url:'../person/person'
				})
			},1000);
		},
		onShareTimeline(res) {
			return{
				title:this.infoList.share_title,
				path:this.curRoutes,
				imageUrl:this.infoList.share_cover,
			}
		},
		onShow() {
			this.autograph = uni.getStorageSync('autograph', 'data');
			this.tokens = uni.getStorageSync('names', 'data').token;   //token
			this.userInfos = uni.getStorageSync('userInfoes', 'data');  //用户信息
			this.signatures = uni.getStorageSync('names', 'data').user_data.signature;  //签名
			console.log(this.userInfos);  
			
			this.autograph = uni.getStorageSync('autograph', 'data');
			console.log(this.tokens)
			this.myworks();
			
		},
		methods: {
			modify(){
				this.showes = true
			},
			determine(){
				console.log(this.values)
				if(this.values == ''){
					uni.showToast({
						title:'请输入签名',
						icon:'none'
					})
				}
				uni.request({
					url: common.WEBSITE_URL + '/api/member/updateUserInfo/platform/' + common.CHANNEL + '?token=' +
						this.tokens,
					header: {
						'Content-Type': 'application/x-www-form-urlencoded'
					},
					method: 'POST',
					data: {
						signature: this.values
					},
					success: (res) => {
						if (res.data.code == 0) {
							this.userInfos.signature = this.values;
							this.autograph = this.values;
							this.showes = false;
							uni.showToast({
								title:'签名修改完成',
								icon:'none'
							})
							uni.setStorage({
								key: 'autograph',
								data: this.values,
							});
						}else{
							uni.showToast({
								title:res.data.data,
								icon:'none'
							})
						}
					}
				})
			},
			logins(){
				uni.navigateTo({
					url:'../login/login'
				})
			},
			indexJump(name, option) {
				uni.navigateTo({
					url: `/pages/${name}/${name}?type=${option}`
				})
			},
			myworks(){
				uni.request({
					url: common.WEBSITE_URL + '/api/competition/getAllSubmission?token='+this.tokens+'&channel='+ common.CHANNEL,
					success: (res) =>{
						if(res.data.code == 0){
							console.log(res.data.data);
							this.articles = res.data.data.article;  //图文
							this.miniapps = res.data.data.miniapp;  //小程序
							this.videos = res.data.data.video;  //视频  
							this.allarticles = res.data.data;
							console.log(uni.getStorageSync('type', 'data'));
							if(this.articles.length>0) {
								console.log(this.articles)
								uni.setStorageSync('type','article');
							}
							else if(this.miniapps.length>0) {
								uni.setStorageSync('type','miniapp');
							}
							else if(this.videos.length>0) {
								uni.setStorageSync('type','video');
							}
							console.log(uni.getStorageSync('type', 'data'));
						}
					}
				})
			},
			getEntryList(){
				uni.request({
					url:common.WEBSITE_URL + '/api/member/getEntryList/platform/'+ common.CHANNEL,
					success: (res) =>{
						if(res.data.code == 0){
							this.lists = res.data.data;
							console.log(res.data.data);
						}
					}
				})	
			},
			taps(index){
				if(index.id == 51){
					uni.navigateTo({
						url:'../official_news/official_news?id='+ 1
					})
				}else if(index.id == 60){
					uni.makePhoneCall({
						phoneNumber: index.entry_url
					})
				}
			},
			onPageScroll(res) {
				//获取距离顶部距离
				let scrollTop = res.scrollTop;
				if(scrollTop <= 10) {
					this.top_background = "background:none;color:#fff;";
					this.top_background_imgurl = "../../static/assets/arrow-left-bold1.png";
					
				}
				else {
					this.top_background = "background:rgba(255,255,255,1);color:#333;";
					this.top_background_imgurl = "../../static/assets/arrow-left-bold.png";
				}
			},
		}
	}
</script>

